.container {
  position: relative;
}

.thumb {
  position: relative;
  width: 400px;
  height: 400px;
  margin-bottom: 20px;
}

.layout {
  position: absolute;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.icon-robot {
  position: absolute;
  display: block;
}

.btns {
  display: flex;
  justify-content: center;

  & > button {
    margin-right: 10px;
  }

  & img {
    width: 30px;
    height: 30px;
    fill: black;
    cursor: pointer;
  }
}

.form {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 28px;

  & .input {
    width: 150px;
    height: 50px;
    font-size: 28px;
  }
}
